*,
*::before,
*::after {
	box-sizing: inherit;
}

html {
	box-sizing: border-box;
}

body {
	font-family: $font-family-gotham-pro;
	font-weight: 400;
	-webkit-font-smoothing: antialiased;
	color: $color-text-main;
}

.content-wrapper {
	width: $site-width-default;
	margin: 0 auto;
}
.content-padding {
	padding-left: $content-padding;
}

.section-title {
	font-size: 44px;
	line-height: 42px;
	margin: 0;
	cursor: default;
}

[v-cloak] {
	display: none;
}

.line {
	position: absolute;
	width: 1px;
	background: $color-border;
	height: 100px;

	&--on-right {
		right: -1px;
	}

	&--on-left {
		left: 0;
	}

	&--on-block-top {
		top: 0;
	}

	&--on-block-bottom {
		top: 100%;
	}

	&--on-block-bottom-to-top {
		bottom: 0;
	}

	&--color-white {
		background: $color-white;
	}
}

.circle {
	border-radius: 50%;
	position: absolute;
	transform: translate(-50%, -50%);
	border: 1px solid $color-border;
}
