.contacts {
	height: 340px;
	display: flex;
	font-size: 15px;
	line-height: 25px;

	&__column {
		width: 50%;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;

		&:first-child {
			background: $color-beige-2;
		}

		&:last-child {
			background: $color-beige-5;
		}
	}

	&__numbers {
		display: flex;
		align-items: center;
	}

	&__work-time {
		margin-bottom: 18px;
	}

	.placement {
		display: flex;
		align-items: center;
		padding: 0 31px 20px;
		margin-bottom: 19px;
		border-bottom: 1px solid rgba($color-white, 0.8);

		.svg-icon-slash {
			width: 40px;
			height: 40px;
			margin-left: 29px;
			margin-right: 27px;
			stroke: $color-black;
		}
	}

	.header__divider {
		margin-left: 18px;
		margin-right: 18px;
	}

	a {
		color: $color-black;
		text-decoration: none;
	}
}
