.footer {
	height: 80px;
	padding-left: 40px;
	padding-right: 40px;
	display: flex;
	align-items: center;
	justify-content: space-between;
	font-size: 15px;
	color: $color-white-soft;
	background: $color-black;

	a {
		color: inherit;
		text-decoration: none;

		&:hover {
			text-decoration: underline;
		}
	}
}
