.about {
	background: $color-beige-1;
	font-size: 16px;
	line-height: 30px;
	font-weight: 400;
	position: relative;

	.section-title {
		padding-top: 106px;
		padding-bottom: 72px;
	}
	// Grid elements
	.text {
		padding-right: $content-padding;

		&--left {
			grid-column: 2 / 3;
		}

		&--center {
			grid-column: 5 / 7;
		}

		p {
			margin-top: 0;
			margin-bottom: 0;

			&:not(:last-child) {
				margin-bottom: 3em;
			}
		}
	}

	.image {
		&--type-1 {
			grid-column: 4 / 9;
		}

		&--type-2 {
			grid-column: 1 / 3;
		}

		&--type-3 {
			grid-column: 7 / 8;
		}

		&--type-4 {
			grid-column: 6 / 8;
		}

		img {
			display: block;
			width: 100%;
			height: auto;
		}
	}

	.content-wrapper {
		position: relative;
		border-left: 1px solid $color-border;
	}

	._line-content-1 {
		left: $content-padding + $services-grid-width;
	}

	._line-content-2 {
		left: $content-padding + $services-gap + $services-grid-width * 2;
	}

	._line-content-3 {
		left: 320px;
	}

	._line-content-4 {
		left: 980px;
	}
}


// Content templates
.about-grid {
	margin-left: calc((100vw - #{$site-width-default}) / 2);
	display: grid;
	grid-template-columns: 100px 400px 20px 65px 60px 380px 340px 1fr;
	grid-template-rows: auto;
	grid-column-gap: 0;
	grid-row-gap: 0;
	justify-items: stretch;
	align-items: center;
	border-left: 1px solid $color-border;

	&__divider {
		grid-column: 1 / 8;

		&--type-1 {
			height: 220px;
		}

		&--type-2 {
			height: 160px;
		}

		&--type-3 {
			height: 140px;
		}
	}
}
