// stylelint-disable scss/dollar-variable-empty-line-before

// BASE
$base-transition: 0.3s;

// COLORS
$color-black: #363636;
$color-gray: rgba($color-black, 0.2);
$color-light-gray: #E4E2DE;
$color-white: #fff;
$color-orange: #ed6b4d;
$color-orange-soft: rgba($color-orange, 0.2);
$color-beige-1: #FCF9F5;
$color-beige-2: #F5EEE4;
$color-beige-3: #EDE2D4;
$color-beige-4: #FCFBF9;
$color-beige-5: #EDE3D4;
$color-blue: #566E8E;
$color-white-soft: rgba(#ECEDED, 0.7);

$background-circle-small: $color-beige-3;
$background-circle-big: $color-beige-2;
$background-hero-slider: $color-beige-1;
$background-services: $color-beige-2;
$background-about: $color-beige-4;
$background-contacts-left: $color-beige-2;
$background-contacts-right: $color-beige-5;
$background-footer: $color-black;
$background-button: $color-orange;
$color-border: rgba($color-gray, 0.12);
$color-border-input: $color-light-gray;
$color-text-main: $color-black;
$color-text-footer: rgba($color-white, 0.7);

// SIZES
$site-width-default: 1360px;
$header-height: 60px;
$content-padding: 100px;
$services-grid-width: 420px;
$services-gap: 20px;

// FONTS
$font-family-gotham-pro: 'GothamPro', sans-serif;
