.popup-outer {
	position: fixed;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	background: rgba($color-black, 0.8);
	z-index: 10;
	padding: 50px;
	overflow: hidden;
	overflow-y: auto;
	visibility: hidden;
	opacity: 0;
	transition: opacity $base-transition, visibility $base-transition;

	&.showed {
		visibility: visible;
		opacity: 1;
	}
}

.popup {
	$root-class: &;

	background: $color-beige-1;
	margin: 0 auto;
	position: relative;
	overflow: hidden;
	max-height: 0;
	opacity: 0;
	transform: translateY(300px);
	transition: opacity $base-transition, transform $base-transition, max-height $base-transition;

	&--form {
		width: 680px;
		padding: 90px 100px 100px;

		&:before {
			content: "";
			display: block;
			width: 1120px;
			height: 1120px;
			border-radius: 50%;
			background: $background-circle-big;
			position: absolute;
			top: -357px;
			right: 70px;
			transform: translate(50%, -50%);
			z-index: 1;
		}

		&:after {
			content: "";
			display: block;
			width: 992px;
			height: 992px;
			border-radius: 50%;
			background: $background-circle-small;
			position: absolute;
			top: -360px;
			right: -114px;
			transform: translate(50%, -50%);
			z-index: 1;
		}
	}

	&--result {
		background: $color-white;
		width: 500px;
		padding: 60px 80px;
		text-align: center;

		#{$root-class}__title {
			margin-top: 60px;
			margin-bottom: 24px;
		}

		p {
			line-height: 28px;
			font-size: 16px;
		}
	}

	&.showed {
		max-height: 2000px;
		opacity: 1;
		transform: translateY(0);
	}

	&__inner {
		position: relative;
		z-index: 2;
	}

	&__title {
		font-size: 40px;
		text-align: center;
		margin-bottom: 60px;
	}

	.close-button {
		width: 40px;
		height: 40px;
		padding: 13px;
		background: $color-orange;
		cursor: pointer;
		border: none;
		outline: none;
		position: absolute;
		top: 0;
		right: 0;
		z-index: 3;

		svg {
			width: 100%;
			height: 100%;
			fill: $color-white;
		}
	}
}
