.form {
	$root-class: &;

	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	grid-template-rows: auto;
	justify-items: stretch;
	align-items: start;
	grid-column-gap: 40px;
	grid-row-gap: 50px;

	&__field {
		display: flex;
		flex-direction: column;
		align-items: flex-start;

		&--expanded {
			grid-column: 1 / 4;
		}

		&--medium {
			grid-column: 1 / 3;
		}

		&--medium-2 {
			grid-column: 2 / 4;
		}

		&--small {
			grid-column: 1 / 2;
		}
	}

	&__label {
		font-weight: 500;
		text-transform: uppercase;
		font-size: 12px;
		position: relative;
		margin-bottom: 20px;
		white-space: nowrap;

		&--required {
			&:before {
				content: "";
				display: block;
				width: 4px;
				height: 4px;
				position: absolute;
				top: -2px;
				right: -6px;
				border-radius: 50%;
				background: $color-orange;
			}
		}
	}

	.jq-selectbox,
	&__input {
		width: 100%;
		border: none;
		background: none;
		border-bottom: 1px solid $color-border-input;
		font-size: 16px;
		color: $color-black;
		height: 40px;
		min-height: 40px;
		max-height: 5 * 40px;
		line-height: 40px;
		transition: border-bottom-color $base-transition;
		outline: none;
		resize: vertical;
		padding: 0;

		&.opened,
		&:focus {
			border-bottom-color: $color-black;
		}

		&--transparent {
			border-bottom: none;
		}
	}

	&__select {
		width: 100%;
		cursor: pointer;
	}

	.jq-selectbox {
		&__select {
			display: flex;

		}

		&__select-text {
			flex-grow: 1;
		}

		&__trigger {
			padding-left: 5px;
			width: 12px;
			flex-shrink: 0;
			background: url("/images/sprites/svg/arrow.svg") center center no-repeat;
		}

		&__dropdown {
			background: $color-white;
			top: 40px !important;
			min-width: 100%;
			padding: 17px 0;
			border: 1px solid $color-border;

			ul {
				padding: 0;
				margin: 0;
			}

			li {
				padding-left: 34px;
				padding-right: 34px;
				transition: color $base-transition;

				&.sel,
				&:hover {
					color: $color-orange;
				}

				&.sel {
					background: rgba($color-orange, 0.1);
				}
			}
		}
	}

	&__expander {
		display: flex;
		align-items: center;
		font-size: 13px;
		border-bottom: 1px  dashed $color-border;
		cursor: pointer;

		#{$root-class}__label {
			margin-bottom: 0;

			& + * {
				flex-grow: 1;
				text-align: right;
				overflow: hidden;
				text-overflow: ellipsis;
				white-space: nowrap;
				padding-left: 10px;
				font-size: inherit;
			}
		}
	}

	&__caption {
		font-size: 15px;
		opacity: 0.5;
		padding-top: 12px;
	}
}
