.datetimepicker {

	&__container {
		height: 280px;
		display: flex;
	}

	.datepicker-container {
		flex-grow: 1;
		padding-right: 35px;
		border-right: 1px dashed $color-border;
		width: 305px;
	}

	.timepicker-container {
		flex-shrink: 0;
		padding-left: 35px;
		padding-right: 20px;
		width: 190px;
		text-align: center;
		display: flex;
		flex-direction: column;

		.date {
			font-size: 13px;
			font-weight: 500;
			height: 40px;
			line-height: 40px;
			margin-bottom: 15px;
		}

		.items {
			overflow: hidden;
			overflow-y: auto;
		}

		.item {
			width: 100%;
			height: 40px;
			line-height: 40px;
			cursor: pointer;
			border: 1px solid $color-border;
			transition: background $base-transition, border-color $base-transition, color $base-transition;
			user-select: none;

			&:not(:last-child) {
				margin-bottom: 15px;
			}

			&:hover {
				background: rgba($color-border, 0.1);
			}

			&.selected {
				border-color: $color-orange;
				background: $color-orange;
				color: $color-white;
				font-weight: 500;
			}
		}
	}

	.form__expander {
		margin-bottom: 30px;
	}
}

.pika-single {
	z-index: unset;
	background: none;
	border: none;
	color: inherit;
	font-family: inherit;
}

.pika-lendar {
	width: 100%;
	margin: 0;
}

.pika-prev,
.pika-next {
	width: 39px;
	height: 39px;
	background-size: auto;
	color: transparent;
	background: url("/images/sprites/svg/arrow.svg") center center no-repeat;
	opacity: 1;
}

.pika-prev {
	transform: rotate(90deg);
}

.pika-next {
	transform: rotate(-90deg);
}

.pika-table {
	th {
		color: inherit;
		font-weight: 500;
		text-transform: uppercase;
		line-height: 39px;

		abbr {
			text-decoration: none;
		}
	}
}

.pika-button {
	background: none;
	color: inherit;
	font-size: 13px;
	line-height: 39px;
	text-align: center;
	padding: 0;
	border-radius: 50% !important;
	transition: color $base-transition, background $base-transition;

	&:hover {
		background: rgba($color-orange, 0.2);
		color: inherit;
	}

	@at-root .is-selected & {
		font-weight: 500;
		background: $color-orange;
		box-shadow: none;
		color: $color-white;
	}

	@at-root .is-disabled & {
		background: none;
		color: inherit;
	}

	@at-root .is-today & {
		font-weight: 500;
		color: $color-orange;
	}
}

.pika-label {
	padding: 0 3px;
	line-height: 39px;
	font-weight: 500;
	text-transform: uppercase;
	background: none;
	z-index: unset;
}
