.slider {
	position: relative;
	//overflow: hidden;

	.content-padding + &__pagination {
		&:before {
			content: "";
			flex-shrink: 0;
			width: $content-padding;
		}
	}

	&__pagination {
		display: flex;
		justify-content: flex-start;
		position: absolute;
		bottom: 0;
		left: 0;
		height: 120px;
		//overflow: hidden;
		//overflow-x: auto;
		max-width: 100%;
		//scroll-behavior: smooth;

		.slider-pagination-item {
			width: 180px;
			height: 120px;
			position: relative;
			cursor: pointer;
			background-color: $color-white-soft;
			flex-shrink: 0;
			background-position: center;
			background-repeat: no-repeat;
			background-size: cover;

			&:before {
				content: "";
				display: block;
				width: 100%;
				height: 100%;
				position: absolute;
				top: 0;
				left: 0;
				z-index: 1;
				opacity: 0.5;
				background: $color-black;
				transition: opacity $base-transition;
			}

			&--active {
				&:before {
					opacity: 0;
				}

				.slider-pagination-item__number {
					color: $color-orange;
				}
			}

			&__inner {
				overflow: hidden;
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
			}

			&__progress {
				position: absolute;
				top: 0;
				left: 0;
				z-index: 2;
				background: $color-white-soft;
				height: 100%;
				width: 100%;
				transform: translateX(-100%);
				transition: transform linear $base-transition;

				&:before {
					content: "";
					display: block;
					width: 100%;
					height: 1px;
					background: $color-orange;
					position: absolute;
					left: 0;
					bottom: 20px;
					z-index: 3;
				}
			}

			&__number {
				position: absolute;
				top: -40px;
				left: 0;
				font-size: 13px;
				line-height: 13px;
				color: rgba($color-black, 0.6);
				transition: color $base-transition;
			}
		}

		.placement {
			background: $color-blue;
			display: flex;
			align-items: center;
			font-size: 15px;
			line-height: 25px;
			color: $color-white;
			padding: 20px 47px;
			position: relative;
			flex-shrink: 0;

			&:before {
				content: "";
				display: block;
				height: 1px;
				width: calc(100% - 40px);
				background: rgba($color-white, 0.2);
				position: absolute;
				bottom: 20px;
				left: 20px;
			}

			img {
				width: 125px;
				height: 78px;
			}

			.svg-icon-slash {
				width: 40px;
				height: 40px;
				margin-left: 29px;
				margin-right: 27px;
			}

			span {
				cursor: default;
				user-select: none;
			}
		}
	}
}

.slider-item {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: space-between;
	height: 720px;

	&--changing-slide {
		animation: changingSlide 1.4s;
	}

	&__image {
		position: absolute;
		top: 0;
		left: 50%;
		transform: translateX(-50%);
		width: 100%;
		height: 100%;
		background-size: cover;
		background-position: center;
		background-repeat: no-repeat;

		&.image-to-right {
			width: 100vw;
			background-size: auto 100%;
			background-position: center right;
		}
	}

	&__content {
		max-width: 450px;
		position: relative;
	}

	&__title {
		font-size: 65px;
		line-height: 80px;
		font-weight: 300;
		margin: 0 0 36px;
		cursor: default;

		.bordered {
			display: inline-block;
			border-bottom: 1px solid rgba($color-black, 0.12);
		}
	}

	&__by {
		font-size: 18px;
		line-height: 18px;
		position: relative;

		&:before {
			content: "";
			display: block;
			width: 20px;
			height: 1px;
			background: $color-text-main;
			position: absolute;
			top: calc(50% - 1px);
			left: -40px;
		}
	}
}

@keyframes changingSlide {
	0% {
		opacity: 1;
	}
	50% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
