.services {
	$parent: &;
	background: $color-beige-2;

	&__items {
		display: grid;
		grid-template-columns: repeat(3, $services-grid-width);
		grid-auto-rows: 600px;
		gap: $services-gap;
	}

	&__item {
		background: $color-white;
		padding: 40px;
		position: relative;
		cursor: default;

		//&:hover {
		//	#{$parent}__item-arrow {
		//		opacity: 1;
		//	}
		//}
	}

	&__coming-soon {
		background: $color-blue;
		color: $color-white;
		text-transform: uppercase;
		font-size: 12px;
		font-weight: 400;
		line-height: 40px;
		padding-left: 18px;
		padding-right: 18px;
		position: absolute;
		top: 20px;
		right: 20px;
	}

	&__item-number {
		font-size: 44px;
		line-height: 42px;
		color: $color-orange;
		margin-bottom: 46px;
	}

	&__item-title {
		font-size: 18px;
		line-height: 17px;
		margin-bottom: 57px;
	}

	&__image {
		background: $color-beige-1;
		width: 340px;
		height: 340px;
		border-radius: 50%;
		overflow: hidden;
		object-fit: contain;

		img {
			display: block;
			pointer-events: none;
		}
	}

	&__item-arrow {
		background: $color-orange;
		width: 60px;
		height: 60px;
		position: absolute;
		bottom: 0;
		right: 0;
		padding: 21px;
		opacity: 0;
		transition: opacity $base-transition;

		svg {
			width: 100%;
			height: 100%;
			fill: $color-white;
		}
	}

	.section-title {
		padding-top: 106px;
		padding-bottom: 92px;
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	.content-wrapper {
		position: relative;
		padding-bottom: 120px;
	}

	._line-content {
		left: $content-padding + $services-grid-width;
	}
}
