.header {
	border-top: 1px solid $color-border;
	border-bottom: 1px solid $color-border;
	display: flex;
	height: $header-height;

	&__block {
		text-align: center;
		position: relative;

		&:not(:last-of-type) {
			border-right: 1px solid $color-border;
		}

		&--growable {
			flex-grow: 1;
			padding-left: 40px;
			padding-right: 40px;
		}

		&--row-view {
			display: flex;
			align-items: center;
			justify-content: center;
		}
	}

	&__link {
		text-decoration: none;
		color: $color-text-main;
		transition: color $base-transition;
		display: block;
		font-size: 15px;
		line-height: 19px;
		padding: 4px 14px;
		position: relative;

		&:hover {
			color: $color-orange;
		}

		&:before {
			content: "";
			width: 0;
			height: 17px;
			background: $color-orange-soft;
			position: absolute;
			left: 4px;
			top: 5px;
			transition: width $base-transition;
		}

		&--active {
			color: $color-orange;

			&:before {
				width: 21px;
			}
		}

		.svg-icon-arrow {
			width: 11px;
			height: 7px;
			fill: currentColor;
			display: inline-block;
			vertical-align: middle;
			margin-left: 7px;
		}
	}

	&__divider {
		width: 1px;
		height: 11px;
		margin: 0 3px;
		background: $color-border;
	}

	.button {
		margin: -1px;
	}
}
