.hero-slider-wrapper {
	padding-top: 42px;
	background: $background-hero-slider url("/images/hero-slider-back.svg") right center no-repeat;
	background-size: cover;
	position: relative;
	overflow: hidden;

	&__label {
		display: flex;
		flex-direction: column;
		align-items: center;
		position: absolute;
		top: 0;
		right: 0;
		height: 100%;
		padding-top: $header-height;

		.border {
			flex-grow: 1;
			width: 1px;
			background: $color-border;
		}

		.text {
			flex-shrink: 0;
			writing-mode: vertical-rl;
			transform: rotate(-180deg);
			font-size: 12px;
			line-height: 12px;
			width: 12px;
			letter-spacing: 0.3em;
			text-transform: uppercase;
			pointer-events: none;
			user-select: none;
			padding-top: 38px;
			padding-bottom: 33px;
		}
	}

	.content-wrapper {
		border-left: 1px solid $color-border;
		position: relative;
		z-index: 2;
	}
}
